.btn {
  transition: all 150ms;
}
.abs-hori-align {
  position: absolute;
  left: 50%;
}
.abs-hori-align > * {
  left: -50%;
}
.circle-badge {
  border-radius: 50%;
  border: 6px solid lightgray;
  padding: 0;
}
html,
body {
  font-size: 18px;
  font-family: "Lato", "Microsoft JhengHei", "Helvetica Neue", Helvetica, Arial, sans-serif !important;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Lato", "Microsoft JhengHei", "Helvetica Neue", Helvetica, Arial, sans-serif !important;
}
.progress > div {
  height: 20px;
}
